// ResumeLayer Component
#resume {
  h2 {
    font-weight: 900;
    font-size: 40px;
    margin-bottom: 50px;
    @media (max-width: 1023px) {
      font-size: 36px;
      margin-bottom: 40px;
    }
  }

  .layer_box {
    margin-bottom: 30px;
    h4 {
      font-size: 20px;
      font-weight: 900;
      margin-bottom: 8px;
    }
    .resume_info {
      margin-left: 2px;
      border-left: 3px solid var(--black);
      .line_box {
        margin-left: 14px;
        margin-bottom: 14px;
        p {
          color: var(--black300);
          &:first-child {
            font-weight: 700;
          }
        }
        // Skills Component
        .name_box {
          display: flex;
          justify-content: space-between;
          margin-bottom: 6px;
        }
        .gauge_box {
          width: 100%;
          height: 10px;
          background-color: var(--gray200);
          margin-bottom: 30px;
          .gauge_bar {
            height: 100%;
            background-color: var(--black300);

            animation-name: score_gauge;
            animation-delay: none;
            animation-timing-function: ease-out;
            animation-iteration-count: 1;
            animation-duration: 2s; /* 애니메이션 지속 시간 */
            animation-fill-mode: forwards; /* 애니메이션이 끝난 후 상태 유지 */
          }
        }
      }
    }
  }

  @keyframes score_gauge {
    0% {
      width: 0%;
    }
    100% {
      width: var(--score-value);
    }
  }
}
